export default {
	name: 'tfgg-verify',
	data() {
		return {
			x: 0,//初始距离
			oldx:0,//移动的距离
			img:'0',//显示哪张图片
			left:'',//随机拼图的最终X轴距离
			top:'',//拼图的top距离
			lefttop:'',//拼图内容的top距离
			shows:false
		};
	},
	mounted() {
		this.shuaxinVerify()
	},
	methods: {
		//刷新验证
		shuaxinVerify(){
			var gl = Math.random();
			this.left = uni.upx2px(500)*gl>uni.upx2px(250)?(uni.upx2px(500)*gl):uni.upx2px(250);//生成随机X轴最终距离
			this.top = -(uni.upx2px(25)+uni.upx2px(343)-(uni.upx2px(263)*gl));//生成随机Y轴初始距离
			this.lefttop = uni.upx2px(263)*gl;//生成随机Y轴初始距离
			if(gl<=0.2){
				this.img=1
			}if(gl>0.2&&gl<=0.4){
				this.img=2
			}if(gl>0.4&&gl<=0.6){
				this.img=3
			}if(gl>0.6&&gl<=0.8){
				this.img=4
			}if(gl>0.8&&gl<=1){
				this.img=5
			}
		},
		/* 滑动中 */
		onMove(e) {
			this.oldx = e.detail.x;
		},
		/* 滑动结束 */
		onEnd() {
			if(Math.abs(this.oldx-this.left)<=5){
				uni.showToast({
					title: '验证成功'
				});
				this.$emit("result",true);
				this.hide();
			}else{
				uni.showToast({
					title: '验证失败'
				});
				this.shuaxinVerify()
				this.reset()
			}
		},
		/* 重置 */
		reset(){
			console.log('重置');
			this.x = 1;
			this.oldx = 1;
			setTimeout(()=>{
				this.x = 0;
				this.oldx = 0;
			},300)
		},
		show(){
			this.shows=true;
		},
		hide(){
			this.shows=false;
		}
	}
}
